import React, { useState, useRef } from 'react';
import "./App.css";

function Todos(props) {
    const database = [
      { text: 'первая задача', author: 'admin', completed: true },
      { text: 'вторая задача', author: 'admin', completed: false },
      { text: 'третья задача', author: 'admin', completed: false },
    ];

    const [todos, setTodos] = useState(database); 

    function delToDo(indexOfItem) {
      setTodos(todos.filter((_, index) => {
          if(index === indexOfItem) {
              return false;
          }
          return true;
      }))
    }

    

    const input = useRef(null);

    const todosToPrint = todos.map((item, key) => {
      return (
        <div key={key} className="todo"> 
          {item.text}
          <button className="del" onClick={() => delToDo(key)}>
            Удалить
        </button>
        </div>
      )
    })

    function addToDo() {
      setTodos([
        ...todos, 
        { text: input.current.value }]);

        input.current.value = '';
    }


    return (
        <div className="todos">
          <div className="input">
            <input type="text" ref={input}/>
            <button className="button" onClick={addToDo}>
              Добавить
            </button>
          </div>
          <div className="items">
            {todosToPrint}
          </div>
        </div>
    );
}

export default Todos;